import { Component, Inject } from "@angular/core"
import { MAT_DIALOG_DATA } from "@angular/material"

import { Ng2ImgMaxService } from 'ng2-img-max'

@Component({
    selector: 'dialog-edit-truck',
    templateUrl: 'dialog-edit-truck.html'
})

export class DialogEditTruck {
    imageLocalSTNK: any[]
    imageLocalInterior: any[]
    imageLocalFront: any[]
    imageLocalSide: any[]

    constructor(
        @Inject(MAT_DIALOG_DATA)
        private data: any,
        private ng2ImgMax: Ng2ImgMaxService,
    ) { }

    selectedSTNK(file: any) {
        // this.attach_banner.setValue(file.target.value)

        if (file.target.files && file.target.files[0]) {
            this.ng2ImgMax.resizeImage(file.target.files[0], 512, 512)
                .subscribe(result => {
                    // this.realImageSTNK = result
                    var reader = new FileReader()
                    reader.onload = (event: any) => {
                        this.imageLocalSTNK = event.target.result
                    }
                    reader.readAsDataURL(file.target.files[0])
                })
        } else {
            this.imageLocalSTNK = undefined
        }
    }

    selectedInterior(file: any) {
        // this.attach_banner.setValue(file.target.value)

        if (file.target.files && file.target.files[0]) {
            this.ng2ImgMax.resizeImage(file.target.files[0], 512, 512)
                .subscribe(result => {
                    // this.realImageInterior = result
                    var reader = new FileReader()
                    reader.onload = (event: any) => {
                        this.imageLocalInterior = event.target.result
                    }
                    reader.readAsDataURL(file.target.files[0])
                })
        } else {
            this.imageLocalInterior = undefined
        }
    }

    selectedFront(file: any) {
        // this.attach_banner.setValue(file.target.value)

        if (file.target.files && file.target.files[0]) {
            this.ng2ImgMax.resizeImage(file.target.files[0], 512, 512)
                .subscribe(result => {
                    // this.realImageFront = result
                    var reader = new FileReader()
                    reader.onload = (event: any) => {
                        this.imageLocalFront = event.target.result
                    }
                    reader.readAsDataURL(file.target.files[0])
                })
        } else {
            this.imageLocalFront = undefined
        }
    }

    selectedSide(file: any) {
        // this.attach_banner.setValue(file.target.value)

        if (file.target.files && file.target.files[0]) {
            this.ng2ImgMax.resizeImage(file.target.files[0], 512, 512)
                .subscribe(result => {
                    // this.realImageSide = result
                    var reader = new FileReader()
                    reader.onload = (event: any) => {
                        this.imageLocalSide = event.target.result
                    }
                    reader.readAsDataURL(file.target.files[0])
                })
        } else {
            this.imageLocalSide = undefined
        }
    }
}
